import { NGL3dApp } from '..';

export class Canvas {
    app: NGL3dApp;

    gl: WebGL2RenderingContext;
    canvas: HTMLCanvasElement;

    constructor(app: NGL3dApp) {
        this.app = app;

        this.canvas = document.createElement('canvas');
        if (this.app.host) {
            this.app.host.appendChild(this.canvas);
            this.canvas.width = this.app.host.clientWidth;
            this.canvas.height = this.app.host.clientHeight;
        }
        const gl = this.canvas.getContext('webgl2');
        if (gl !== null) {
            this.gl = gl;
            this.gl.cullFace(gl.BACK);								    // Back is also default
            this.gl.frontFace(gl.CCW);								    // Dont really need to set it, its ccw by default.
            this.gl.enable(gl.DEPTH_TEST);							    // Shouldn't use this, use something else to add depth detection
            this.gl.enable(gl.CULL_FACE);							    // Cull back face, so only show triangles that are created clockwise
            this.gl.depthFunc(gl.LEQUAL);							    // Near things obscure far things
            this.gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);	// Setup default alpha blending
        }

        this.loop();
    }
    // fFitScreen = function(wp,hp) { return this.fSetSize(window.innerWidth * (wp || 1),window.innerHeight * (hp || 1)); }

    loop() {
        this.clear(this.gl);
        requestAnimationFrame(() => { this.loop(); });

        this.app.renderer.render();
        // console.log('working');
    }
    clear(gl: WebGL2RenderingContext) {

        gl.cullFace(gl.BACK);								// Back is also default
        gl.frontFace(gl.CCW);								// Dont really need to set it, its ccw by default.
        gl.enable(gl.DEPTH_TEST);							// Shouldn't use this, use something else to add depth detection
        gl.enable(gl.CULL_FACE);							// Cull back face, so only show triangles that are created clockwise
        gl.depthFunc(gl.LEQUAL);							// Near things obscure far things
        gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);	// Setup default alpha blending
        gl.clearColor(1.0, 1.0, 1.0, 1.0);	// Set clear color

        // gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
        // gl.clearColor(1.0, 1.0, 1.0, 1.0);
    }
}
