import { Canvas } from './Core/Canvas';
import { Renderer } from './Core/Renderer';
import { Scene } from './Core/Scene';

import { Camera, CameraController } from './WebGL/Camera';

import { GridAxis } from './Geometry/Primatives/GridAxis';

// import { PointScreen } from './Geometry/Primatives/PointScreen';
// import { PointWorld } from './Geometry/Primatives/PointWorld';
import { Quad } from './Geometry/Primatives/Quad';
import { MulitQuad } from './Geometry/Primatives/MulitQuad';

// import { RectScreen } from './Geometry/CustomGeometry/RectScreen';
// import { Rect } from './Geometry/CustomGeometry/Rect';

export class NGL3dApp {

    host: HTMLElement;
    canvas: Canvas;
    renderer: Renderer;
    scene: Scene;

    camera: Camera;
    cameraCtrl: CameraController;

    constructor(id: string) {
        console.log('id: ' + id + ', NGL3dApp 07-1 Init!!!!!');
        const host = document.getElementById(id);
        if (host) { this.host = host; }

        this.scene = new Scene(this);
        this.renderer = new Renderer(this);
        this.canvas = new Canvas(this);

        this.camera = new Camera(this.canvas.gl);
        this.camera.transform.position.set(0, 1, 3);
        this.cameraCtrl = new CameraController(this.canvas.gl, this.camera);

        let dd = new GridAxis(this);
        // console.log(dd);

        // let d = new PointScreen(this);
        // console.log(d);

        // let d1 = new PointWorld(this);
        // console.log(d1);

        let dd2 = new Quad(this);
        // console.log(dd2);

        // let dd3 = new Rect(this);
        // console.log(dd3);

        let dd5 = new MulitQuad(this);
        // console.log(dd5);

    }

}

